html {
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  position: relative;
  font-size: 16px;
}
body {
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  position: relative;
}
.lang-en {
  font-family: "Open Sans", sans-serif;
}
.lang-zh {
  font-family: "Noto Sans SC";
}
.lang-zh .zh-box {
  display: block !important;
}
.lang-zh .en-box {
  display: none !important;
}
.lang-en .en-box {
  display: block !important;
}
.lang-en .zh-box {
  display: none !important;
}
.lang-zh .zh-box1 {
  height: auto !important;
  overflow: auto;
}
.lang-zh .en-box1 {
  height: 0 !important;
  overflow: hidden;
}
.lang-en .en-box1 {
  height: auto !important;
  overflow: auto;
}
.lang-en .zh-box1 {
  height: 0 !important;
  overflow: hidden;
}
.icon-indent-increase {
  font-size: 22px;
}
.gray {
  -webkit-filter: grayscale(100%);
  -moz-filter: grayscale(100%);
  -ms-filter: grayscale(100%);
  -o-filter: grayscale(100%);
  filter: grayscale(100%);
  filter: gray;
}
.heading {
  font-family: "Noto Sans SC";
  font-weight: 300;
  font-size: 1.75rem;
  width: 100%;
}
.para {
  font-family: "Noto Sans SC";
  font-weight: 300;
  font-size: 1rem;
  line-height: 26px;
  color: #677382;
  text-align: left;
}
.para {
  font-family: "Open Sans", sans-serif;
}
.row {
  display: block;
}
.download-btn img {
  height: 60px;
}
.download-btn:hover {
  cursor: pointer;
}
.hidden {
  display: none;
}
.show {
  display: block;
}
.nav {
  padding: 1.9em 0 1.9em 0;
  color: #20242B;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  min-width: 986px;
}
.nav.nav-mobile {
  display: none;
}
.nav .logo {
  float: left;
  display: inline-block;
  margin-left: 40px;
  margin-right: 1.5rem;
}
.nav .logo .logo-link {
  display: block;
}
.nav .logo img {
  height: 33px;
}
.nav .logo:hover {
  cursor: pointer;
}
.nav .logo-light img {
  height: 50px;
}
.nav .menu-box {
  display: none;
}
.nav .navigation.ch {
  font-family: "Noto Sans SC";
  font-weight: 300;
}
.nav .navigation {
  font-family: "Open Sans", sans-serif;
}
.nav .navigation {
  margin-right: 40px;
}
.nav .navigation .nav-opt:last-child {
  padding-right: 0;
}
.nav .navigation .nav-opt-lang {
  display: inline-block;
  padding-left: 32px;
  font-size: 16px;
}
.nav .top-nav .navigation {
  float: right;
}
.nav .nav-opt {
  display: inline-block;
  padding: 0 32px;
  font-size: 16px;
}
.nav .nav-opt:hover {
  cursor: pointer;
}
.nav .nav-opt-blue {
  padding: 4px 12px;
  border: 1px solid #1652F0;
  color: #1652F0;
  border-radius: 4px;
  font-weight: bold;
}
@media screen and (max-width: 414px) {
  .nav.nav-pc {
    display: none;
  }
  .nav.nav-mobile {
    box-sizing: border-box;
    display: block;
    min-width: auto;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 9999;
    padding: 8px 0;
    background-color: #363745;
    border-bottom: 1px solid #15232c;
    color: #fff;
  }
  .nav.nav-mobile .logo {
    margin-left: 0.5rem;
    margin-top: 0;
  }
  .nav.nav-mobile .logo img {
    height: 44px;
  }
  .nav.nav-mobile .menu-box {
    float: right;
    display: block;
    margin-right: 1rem;
    margin-top: 10px;
    line-height: 1;
    color: #fff;
  }
  .nav.nav-mobile .menu-box .iconfont {
    font-size: 22px;
  }
  .nav.nav-mobile .navigation {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 65px;
    right: -100%;
    background-color: #363745;
    border-top: 1px solid #15232c;
    z-index: 999999;
    box-sizing: border-box;
    transition: all 0.4s ease;
    overflow: hidden;
    margin-right: 0;
  }
  .nav.nav-mobile .navigation .nav-opt {
    display: block;
    box-sizing: border-box;
    min-width: 136px;
    height: 48px;
    line-height: 48px;
    padding: 0 16px;
    white-space: nowrap;
    text-align: left;
  }
  .nav.nav-mobile .navigation .nav-opt-blue {
    padding: 0 16px;
    border: 0 none;
    color: #1652F0;
    border-radius: 4px;
    font-weight: bold;
  }
  .nav.nav-mobile .navigation .nav-opt-lang {
    display: block;
    height: 48px;
    line-height: 48px;
    padding: 0 16px;
  }
  .nav.nav-mobile .navigation-logout .link {
    color: #E96239;
  }
}
.lang {
  display: flex;
  align-items: center;
}
.lang:hover {
  cursor: pointer;
}
.english {
  font-family: "Open Sans", sans-serif;
}
.chinese {
  font-family: "Noto Sans SC";
  font-weight: 300;
}
.flag img {
  height: 16px;
  vertical-align: middle;
  margin-right: 5px;
}
.download-now-btn {
  border: 1px solid white;
  border-radius: 50px;
  height: 40px;
  width: 143px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.download-now-btn:hover {
  cursor: pointer;
}
.illustration-bottom {
  width: 85%;
  height: 85%;
  background: url(../images/cloud_footer.png) no-repeat right bottom;
  -webkit-background-size: contain;
  -moz-background-size: contain;
  -o-background-size: contain;
  background-size: contain;
  position: absolute;
  bottom: 0px;
  right: 0px;
  z-index: -2;
}
.footer-cta {
  max-width: 440px;
  margin: 0 auto;
  margin-left: 12%;
  padding-top: 20%;
  text-align: center;
}
.footer .heading.ch {
  font-family: "Noto Sans SC";
  font-weight: 400;
  font-size: 2rem;
}
.footer .heading {
  font-size: 1.75rem;
  font-weight: 400;
}
.footer .para {
  color: #1652F0;
  text-align: center;
  margin-top: 5px;
}
.bot-nav {
  display: block;
  padding: 40px 0 60px 0;
  position: absolute;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
}
.bot-nav .footer-links .links-list {
  display: flex;
  align-items: center;
  justify-content: center;
}
.bot-nav .footer-links .links-list > .item {
  position: relative;
  margin-right: 20px;
  color: #20252b;
}
.bot-nav .footer-links .links-list > .item:last-child {
  margin-right: 0;
}
.bot-nav .footer-links .links-list > .item .item-link {
  display: block;
  width: 32px;
  height: 32px;
  line-height: 32px;
  border: 1px solid #20252b;
  border-radius: 50%;
  text-align: center;
  opacity: 0.4;
}
.bot-nav .footer-links .links-list > .item .item-link:hover {
  opacity: 1;
}
.bot-nav .footer-links .links-list > .item .item-link-dianbao {
  cursor: pointer;
}
.bot-nav .footer-links .links-list > .item .item-link-dianbao:hover .telegram-list {
  display: block;
}
.bot-nav .footer-links .links-list > .item .icon {
  color: #20252b;
}
.bot-nav .footer-links .links-list > .item .icon-dianbao {
  font-size: 16px;
}
.bot-nav .footer-links .links-list > .item .telegram-list {
  display: none;
  box-sizing: border-box;
  border: 1px solid #14181c;
  border-radius: 4px;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  position: absolute;
  bottom: 100%;
  left: 50%;
  transform: translate(-50%);
  background: #363745;
  white-space: nowrap;
  color: #fff;
}
.bot-nav .footer-links .links-list > .item .telegram-list > .item {
  border-bottom: 1px solid #14181c;
  font-size: 13px;
}
.bot-nav .footer-links .links-list > .item .telegram-list > .item:last-child {
  border-bottom: 0 none;
}
.bot-nav .footer-links .links-list > .item .telegram-list > .item .tele-link {
  padding: 4px 12px;
}
.bot-nav .footer-links .links-list > .item .telegram-list-white {
  border: 1px solid #ebeef5;
  background: #fff;
  color: #606266;
}
.bot-nav .footer-links .links-list > .item .telegram-list-white > .item {
  border-color: rgba(0, 0, 0, 0.1);
}
.bot-nav .bot-nav-riht .footer-links {
  padding-right: 40px;
}
.bot-nav .bot-nav-riht .links-list {
  justify-content: flex-end;
}
.bot-nav .bot-nav-riht .links-list .item .item-link {
  border: 1px solid rgba(0, 0, 0, 0.5);
  opacity: 0.8;
}
.bot-nav .bot-nav-riht .links-list .item .icon {
  color: rgba(0, 0, 0, 0.5);
}
.bot-nav-white {
  border-top: 0 none;
}
.bot-nav-white .bot-nav-riht .links-list .item .item-link {
  border: 1px solid #fff;
}
.bot-nav-white .bot-nav-riht .links-list .item .icon {
  color: #fff;
}
@media screen and (max-width: 414px) {
  .bot-nav {
    display: block;
    min-width: auto;
  }
  .bot-nav .logo {
    display: none !important;
  }
  .bot-nav .bot-nav-riht {
    width: 100%;
  }
  .bot-nav .bot-nav-riht .footer-links {
    padding-right: 0;
  }
  .bot-nav .bot-nav-riht .footer-links .links-list {
    justify-content: center;
  }
  .bot-nav .bot-nav-riht .footer-links .links-list > .item {
    margin-right: 12px;
  }
}
.footer-aside .footer-cta {
  margin: 0 auto;
  padding: 240px 0;
}
.footer-aside .footer-cta .download {
  margin-top: 24px;
  justify-content: center;
}
.footer-aside .footer-cta .download-btn img {
  height: 52px;
}
.footer-aside .bot-nav {
  position: relative;
}
.footer-aside .bot-nav .navigation {
  margin: 0 auto;
  text-align: center;
  color: rgba(0, 0, 0, 0.4);
}
@media screen and (max-width: 414px) {
  .footer-aside .bot-nav {
    background-color: #353746;
  }
  .footer-aside .footer-cta {
    padding: 60px 0;
  }
  .footer-aside .bot-nav-riht .item .item-link {
    border: 1px solid #fff !important;
  }
  .footer-aside .bot-nav-riht .icon {
    color: #fff !important;
  }
}
