html {
    width: 100%;
    height: 100%;
    overflow-x: hidden;
    position: relative;
    font-size: 16px;
}

body {
    width: 100%;
    height: 100%;
    overflow-x: hidden;
    position: relative;
}

.lang-en {
    font-family: @font_family_2;
}
.lang-zh {
    font-family: @font_family_1;
}

.lang-zh .zh-box {
    display: block !important;
}

.lang-zh .en-box {
    display: none !important;
}

.lang-en .en-box {
    display: block !important;
}

.lang-en .zh-box {
    display: none !important;
}

.lang-zh .zh-box1 {
    height: auto !important;
    overflow: auto;
}

.lang-zh .en-box1 {
    height: 0 !important;
    overflow: hidden;
}

.lang-en .en-box1 {
    height: auto !important;
    overflow: auto;
}

.lang-en .zh-box1 {
    height: 0 !important;
    overflow: hidden;
}

.icon-indent-increase {
    font-size: 22px;
}

.gray {
    -webkit-filter: grayscale(100%);
    -moz-filter: grayscale(100%);
    -ms-filter: grayscale(100%);
    -o-filter: grayscale(100%);
    filter: grayscale(100%);
    filter: gray;
}

.heading {
    font-family: @font_family_1;
    font-weight: 300;
    font-size: 1.75rem;
    width: 100%;
}

.para {
    font-family: @font_family_1;
    font-weight: 300;
    font-size: 1rem;
    line-height: 26px;
    color: @color_1;
    text-align: left;
}

.para {
    font-family: @font_family_2;
}

.row {
    display: block;
}

.download-btn {
    img {
        height: 60px;
    }
    &:hover {
        cursor: pointer;
    }
}

.hidden {
    display: none;
}

.show {
    display: block;
}

.nav-container {

}

.nav {
    padding: 1.9em 0 1.9em 0;
    color: @color_2;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    min-width: 986px;

    &.nav-mobile {
        display: none;
    }

    .logo {
        float: left;
        display: inline-block;
        margin-left: 40px;
        margin-right: 1.5rem;
        .logo-link {
            display: block;
        }
        img {
            height: 33px;
        }
        &:hover {
            cursor: pointer;
        }
    }

    .logo-light {
        img {
            height: 50px;
        }
    }

    .menu-box {
        display: none;
    }

    .navigation.ch {
        font-family: @font_family_1;
        font-weight: 300;
    }

    .navigation {
        font-family: @font_family_2;
    }

    .navigation {
        margin-right: 40px;
        .nav-opt {
            &:last-child {
                padding-right: 0;
            }
        }
        .nav-opt-lang {
            display: inline-block;
            padding-left: 32px;
            font-size: 16px;
        }
    }

    .top-nav {
        .navigation {
            float: right;
        }
    }

    .nav-opt {
        display: inline-block;
        padding: 0 32px;
        font-size: 16px;
        &:hover {
            cursor: pointer;
        }
        &.lang {
            .lang-select {
                &.active {
                    //color: @color-blue !important;
                }
            }
        }
        &-blue {
            padding: 4px 12px;
            border: 1px solid @color_3;
            color: @color_3;
            border-radius: 4px;
            font-weight: bold;
        }
    }

    @media screen and (max-width: 414px) {
        &.nav-pc {
            display: none;
        }

        &.nav-mobile {
            box-sizing: border-box;
            display: block;
            min-width: auto;
            position: fixed;
            top: 0;
            left: 0;
            z-index: 9999;
            padding: 8px 0;
            background-color: #363745;
            border-bottom: 1px solid #15232c;
            color: #fff;

            .logo {
                margin-left: .5rem;
                margin-top: 0;
                img {
                    height: 44px;
                }
            }

            .menu-box {
                float: right;
                display: block;
                margin-right: 1rem;
                margin-top: 10px;
                line-height: 1;
                color: #fff;
                .iconfont {
                    font-size: 22px;
                }
            }

            .navigation {
                width: 100%;
                height: 100%;
                position: fixed;
                top: 65px;
                right: -100%;
                background-color: #363745;
                border-top: 1px solid #15232c;
                z-index: 999999;
                box-sizing: border-box;
                transition: all 0.4s ease;
                overflow: hidden;
                margin-right: 0;
                .nav-opt{
                    display: block;
                    box-sizing: border-box;
                    min-width: 136px;
                    height: 48px;
                    line-height: 48px;
                    padding: 0 16px;
                    white-space:nowrap;
                    text-align: left;
                    &-blue {
                        padding: 0 16px;
                        border: 0 none;
                        color: @color_3;
                        border-radius: 4px;
                        font-weight: bold;
                    }
                }
                .nav-opt-lang {
                    display: block;
                    height: 48px;
                    line-height: 48px;
                    padding: 0 16px;
                    &.english {
                        //border-top: 1px solid #15232c;
                    }
                }
                &-logout {
                    .link {
                        color: #E96239;
                    }
                }
            }
        }
    }
}

.lang {
    display: flex;
    align-items: center;
    &:hover {
        cursor: pointer;
    }
}

.english {
    font-family: @font_family_2;
}

.chinese {
    font-family: @font_family_1;
    font-weight: 300;
}

.flag {
    img {
        height: 16px;
        vertical-align: middle;
        margin-right: 5px;
    }
}

.download-now-btn {
    border: 1px solid white;
    border-radius: 50px;
    height: 40px;
    width: 143px;
    display: flex;
    align-items: center;
    justify-content: center;
    &:hover {
        cursor: pointer;
    }
}

.illustration-bottom {
    width: 85%;
    height: 85%;
    background: url(../images/cloud_footer.png) no-repeat right bottom;
    -webkit-background-size: contain;
    -moz-background-size: contain;
    -o-background-size: contain;
    background-size: contain;
    position: absolute;
    bottom: 0px;
    right: 0px;
    z-index: -2;
}

.footer-cta {
    max-width: 440px;
    margin: 0 auto;
    margin-left: 12%;
    padding-top: 20%;
    text-align: center;
}

.footer {
    .heading.ch {
        font-family: @font_family_1;
        font-weight: 400;
        font-size: 2rem;
    }
    .heading {
        font-size: 1.75rem;
        font-weight: 400;
    }
    .para {
        color: @color_3;
        text-align: center;
        margin-top: 5px;
    }
}

.bot-nav {
    display: block;
    padding: 40px 0 60px 0;
    position: absolute;
    bottom: 0;
    width: 100%;
    border-top: 1px solid rgba(0, 0, 0, 0.1);

    .footer-links {
        .links-list {
            display: flex;
            align-items: center;
            justify-content: center;
            >.item {
                position: relative;
                margin-right: 20px;
                color: #20252b;
                &:last-child {
                    margin-right: 0;
                }
                .item-link {
                    display: block;
                    width: 32px;
                    height: 32px;
                    line-height: 32px;
                    border: 1px solid #20252b;
                    border-radius: 50%;
                    text-align: center;
                    opacity: .4;
                    &:hover {
                        opacity: 1;
                    }
                    &-dianbao {
                        cursor: pointer;
                        &:hover {
                            .telegram-list {
                                display: block;
                            }
                        }
                    }
                }
                .icon {
                    color: #20252b;
                }
                .icon-dianbao {
                    font-size: 16px;
                }

                .telegram-list {
                    display: none;
                    box-sizing: border-box;
                    border: 1px solid #14181c;
                    border-radius: 4px;
                    box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
                    position: absolute;
                    bottom: 100%;
                    left: 50%;
                    transform: translate(-50%);
                    background: #363745;
                    white-space: nowrap;
                    color: #fff;
                    >.item {
                        border-bottom: 1px solid #14181c;
                        font-size: 13px;
                        &:last-child {
                            border-bottom: 0 none;
                        }
                        .tele-link {
                            padding: 4px 12px;
                        }
                    }
                    &-white {
                        border: 1px solid #ebeef5;
                        background: #fff;
                        color: #606266;
                        >.item {
                            border-color: rgba(0, 0, 0, 0.1);
                        }
                    }
                }
            }
        }
    }
    .bot-nav-riht {
        .footer-links {
            padding-right: 40px;
        }
        .links-list {
            justify-content: flex-end;
            .item {
                .item-link {
                    border: 1px solid rgba(0, 0, 0, .5);;
                    opacity: .8;
                }
                .icon {
                    color: rgba(0, 0, 0, .5);
                }
            }
        }
    }

    &-white {
        border-top: 0 none;
        .bot-nav-riht {
            .links-list {
                .item {
                    .item-link {
                        border: 1px solid #fff;
                    }
                    .icon {
                        color: #fff;
                    }
                }
            }
        }
    }
    @media screen and (max-width: 414px) {
        & {
            display: block;
            min-width: auto;
            .logo {
                display: none !important;
            }
            .bot-nav-riht {
                width: 100%;
                .footer-links {
                    padding-right: 0;
                    .links-list {
                        justify-content: center;
                        >.item {
                            margin-right: 12px;
                        }
                    }
                }
            }
        }
    }
}

.footer-aside {
    .footer-cta {
        margin: 0 auto;
        padding: 240px 0;
        .download {
            margin-top: 24px;
            justify-content: center;
        }
        .download-btn {
            img {
                height: 52px;
            }
        }
    }
    .bot-nav {
        position: relative;
        .navigation {
            margin: 0 auto;
            text-align: center;
            color: rgba(0, 0, 0, .4);
        }
    }
    @media screen and (max-width: 414px) {
        .bot-nav {
            background-color: #353746;
        }
        .footer-cta {
            padding: 60px 0;
        }
        .bot-nav-riht {
            .item {
                .item-link {
                    border: 1px solid #fff !important;
                }
            }
            .icon {
                color: #fff !important;
            }

        }
    }
}



